@import url(//maxcdn.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css);
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
table {
  margin: 2em;
  padding: 2em;
}
th {
  font-size: 1.2em;
  border-bottom: 1px solid black;
  padding: .5em 2em;
}
td {
  padding: .5em 2em;
}
button {
  border-radius: 0;
  border: none;
  padding: .5em 3em;
  margin: .5em 1em;
  color: #fff;
  background-color: #007bff;
  border-color: #007bff;
}
button:hover {
  color: #fff;
  background-color: #0069d9;
  border-color: #0062cc;
}
.modal-mask {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .5);
  display: table;
  -webkit-transition: opacity .3s ease;
  transition: opacity .3s ease;
}
.modal-wrapper {
  display: table-cell;
  vertical-align: middle;
}
.modal-container {
  height: 80%;
  width: 80%;
  overflow-y: auto;
  margin: 0px auto;
  padding: 20px 30px;
  background-color: #fff;
  border-radius: 2px;
  -webkit-box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
          box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
  font-family: Helvetica, Arial, sans-serif;
}
.modal-header h3 {
  margin-top: 0;
  color: #42b983;
}
.modal-body {
  margin: 20px 0;
}
.modal-default-button {
  float: right;
}
.modal-close-button {
  /*float: right;*/
  background-color: grey;
}

/*
 * The following styles are auto-applied to elements with
 * transition="modal" when their visibility is toggled
 * by Vue.js.
 *
 * You can easily play with the modal transition by editing
 * these styles.
 */
.modal-enter {
  opacity: 0;
}
.modal-leave-active {
  opacity: 0;
}
.modal-enter .modal-container,
.modal-leave-active .modal-container {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}

/*https://gist.githubusercontent.com/pom-pom/10255961/raw/1ec68600b358e7719a9b1295756d99cfa53365d0/font-awesome-form-elements.css*/

/*Custom Radio Buttons and Checkboxes using Font Awesome*/
.radio {
  display: block;
}
input[type=radio] {
  display: none;
}
input[type='radio'] + label:before {
  display: inline-block;
  font-family: FontAwesome;
  font-style: normal;
  font-weight: normal;
  line-height: 1;
  font-size: 2em;
  margin-bottom: .5em;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding-right: 8px;
  width: 23px;
}
input[type='radio'].good + label:before {
  content: "\F164";
  color: #D6D5D5;
}
input[type='radio']:checked.good + label:before {
  content: "\F164";
  color: orange;
}
input[type='radio'].bad + label:before {
  content: "\F165";
  color: #D6D5D5;
}
input[type='radio']:checked.bad + label:before {
  content: "\F165";
  color: orange;
}
.radio label  {
  padding-left: 0;
}

